// istanbul ignore file: it is difficult to load images to then test the auto-layout portion of the code here; however, the story for this component covers all scenarios
import type { CSSProperties, ReactEventHandler, SyntheticEvent } from 'react';
import React, { forwardRef, useCallback, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';
import classNames from 'classnames';
import Icon from '../Icon';
import { useEnsureRef } from '../hook';

const StyledRoot = styled.span<ImageProps & TransientProps>`
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  overflow: hidden;

  &.arthur-image--failure {
    & .arthur-image-icon {
      &::after {
        ${(props) => (props.$errorText || props.$alt) && `margin-left: ${props.theme.token.size.spacing['small-2x']};`}
        content: "${(props) => props.$errorText || props.$alt || ''}";
      }
    }
  }
`;

export interface ImageProps {
  className?: string;
  /**
   * Describes the [standard "alt" attribute](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/img#alt) on img elements.
   *
   * @default undefined
   */
  alt?: string;

  /**
   * Controls whether or not the inner image should center and proportion itself to its parent element.
   *
   * @default true
   */
  autoLayout?: boolean;

  /**
   * Describes the text to be displayed after the ImageProps#onError event fires.
   *
   * @default undefined
   */
  errorText?: string;

  /**
   * Describes the height style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  height?: string | number;

  /**
   * Describes the max-height style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  maxHeight?: string | number;

  /**
   * Describes the max-width style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  maxWidth?: string | number;

  /**
   * Describes the min-height style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  minHeight?: string | number;

  /**
   * Describes the min-width style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  minWidth?: string | number;

  /**
   * Describes the [standard "src" attribute](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/img#src) on img elements.
   *
   * @default ""
   */
  src?: string;

  /**
   * Describes the width style of the root element. When provided as a number, it is treated as pixels.
   *
   * @default undefined
   */
  width?: string | number;

  /**
   * Handles the [standard "onerror" event](https://developer.mozilla.org/en-US/docs/Web/API/HTMLElement/error_event) on HTML elements.
   *
   * @default undefined
   */
  onError?: (event: SyntheticEvent<HTMLImageElement>) => void;

  /**
   * Handles the [standard "onload" event](https://developer.mozilla.org/en-US/docs/Web/API/HTMLElement/load_event) on HTML elements.
   *
   * @default undefined
   */
  onLoad?: (event: SyntheticEvent<HTMLImageElement>) => void;
  style?: CSSProperties;
}

interface TransientProps {
  $alt?: string;
  $errorText?: string;
}

const Image = forwardRef<HTMLSpanElement, ImageProps>((props, forwardedRef) => {
  const {
    alt,
    autoLayout,
    className,
    errorText,
    height,
    maxHeight,
    maxWidth,
    minHeight,
    minWidth,
    src,
    style,
    width,
    onError,
    onLoad,
    ...rest
  } = props;

  const rootRef = useEnsureRef(forwardedRef);
  const [loaded, setLoaded] = useState<boolean | null>(null);

  // useEffect(() => {
  //   setLoaded(null);
  // }, [src]);

  useEffect(() => {
    let timeoutId: NodeJS.Timeout;
    function onResize() {
      // resets timeout if applicable
      clearTimeout(timeoutId);

      // throttle resize calculations if resize events are happening in quick succession
      timeoutId = setTimeout(() => {
        rootRef.current && doLayout(rootRef.current);
      }, 150);
    }

    if (autoLayout === true && rootRef.current) {
      window.addEventListener('resize', onResize);
    }

    return () => {
      clearTimeout(timeoutId);
      window.removeEventListener('resize', onResize);
    };
  }, [autoLayout, rootRef]);

  const classes = classNames(className, 'arthur-image', {
    'arthur-image--success': loaded,
    'arthur-image--failure': !loaded,
  });

  const styles = useMemo(() => {
    return {
      height: formatSize(height),
      maxHeight: formatSize(maxHeight),
      maxWidth: formatSize(maxWidth),
      minHeight: formatSize(minHeight),
      minWidth: formatSize(minWidth),
      width: formatSize(width),
      ...style,
    };
  }, [height, maxHeight, maxWidth, minHeight, minWidth, style, width]);

  const handleOnError = useCallback<ReactEventHandler<HTMLImageElement>>(
    (event: SyntheticEvent<HTMLImageElement>) => {
      setLoaded(false);
      onError?.(event);
    },
    [onError],
  );

  const handleOnLoad = useCallback<ReactEventHandler<HTMLImageElement>>(
    (event: SyntheticEvent<HTMLImageElement>) => {
      if (autoLayout === true && rootRef.current) {
        doLayout(rootRef.current);
      }

      setLoaded(true);
      onLoad?.(event);
    },
    [autoLayout, onLoad, rootRef],
  );
  console.log('%c loaded', 'color: magenta;', loaded);

  return (
    <StyledRoot $alt={alt} $errorText={errorText} className={classes} ref={rootRef} style={styles}>
      {!loaded && <Icon className='arthur-image-icon' size='large' value='fa-image-slash' />}
      <img
        alt={alt}
        className='arthur-image-img'
        onError={handleOnError}
        onLoad={handleOnLoad}
        src={src || ''} // REMARK: THIS WILL FORCE THE ERROR EVENT TO FIRE IN THE EVENT THAT NOTHING IS PROVIDED TO ImageProps#src
        {...rest}
      />
    </StyledRoot>
  );
});

export default Image;
Image.displayName = 'Image';

function formatSize(value?: string | number): string | undefined {
  if (typeof value === 'number') {
    return `${value}px`;
  }

  if (Boolean(value) && typeof value === 'string') {
    return value;
  }

  return undefined;
}

function doLayout(root: HTMLSpanElement) {
  const { offsetHeight: rootH, offsetWidth: rootW } = root;

  const img = root.querySelector<HTMLImageElement>('.arthur-image-img');
  if (!img) {
    return;
  }

  const { naturalHeight: imgH, naturalWidth: imgW } = img;

  if (imgH <= 0 || imgW <= 0) {
    return;
  }

  let newW: number | undefined;
  let newH: number | undefined;

  // if landscape img
  if (imgW > imgH) {
    if (rootW > rootH) {
      if (imgW >= rootW) {
        newW = rootW;
      } else {
        newH = rootH;
      }
    } else {
      newH = rootH;
    }
    // if portrait img
  } else if (imgW < imgH) {
    if (rootW < rootH) {
      if (imgH >= rootH) {
        newH = rootH;
      } else {
        newW = rootW;
      }
    } else {
      newW = rootW;
    }
    // if square img
  } else if (rootW < rootH) {
    newH = rootH;
  } else {
    newW = rootW;
  }

  if (newW) {
    img.style.width = `${newW}px`;
  }

  if (newH) {
    img.style.height = `${newH}px`;
  }

  // the above centering/scaling works most of the time,
  // but it does fail to completely fill the root element;
  // the below conditionally applies a minor adjustment if necessary
  if (newH && root.offsetWidth > img.offsetWidth) {
    img.style.width = `${root.offsetWidth}px`;
  } else if (newW && root.offsetHeight > img.offsetHeight) {
    img.style.height = `${root.offsetHeight}px`;
  }
}
